        { text: "Cn Th", value: "CT" },
        { text: " Nng", value: "Na" },
        { text: "k Lk", value: "L" },
        { text: "k Nng", value: "No" },
        { text: "in Bin", value: "B" },
        { text: "ng Nai", value: "N" },
        { text: "ng Thp", value: "T" },
        { text: "Gia Lai", value: "GL" },
